import React, { Component } from 'react'
import { NavBar, Icon,Grid, Tabs} from 'antd-mobile';
const tabs = [
    { title: '推荐' },
    { title: '冬季'},
    { title: '宜家' },
    { title: '小清新' },
    { title: '小户型' },
    { title: '个性色彩' },
];
const data1 = Array.from([1,2,3,4]).map((item) => ({
    src: `./images/${item+6}.png`,
}));
export default class Idea extends Component {
    render() {
        return (
            <div>
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#fff'}}
                    rightContent={[                        
                        <Icon key="0" type="search" />,
                      ]}
                    >灵感
                </NavBar> 
                <Tabs tabs={tabs}
                    initialPage={0}
                    // onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    // onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                    >
                    <div style={{background:'#eee',padding:'20px'}}>
                        <Grid style={{background:'#eeeeee'}} data={data1}
                            columnNum={2}                        
                            renderItem={dataItem => (                                                              
                                <img src={dataItem.src} style={{ width:'80%', height: 'auto' }} alt="" />                                
                            )}
                        />
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        冬季
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        宜家
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        小清新
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        小户型
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        个性色彩
                    </div>
                </Tabs>
            </div>
        )
    }
}
