import React, { Component } from 'react'
import { NavBar, Grid,Carousel } from 'antd-mobile';


  
const data1 = Array.from([1,2,3]).map((item) => ({
src: `./images/${item+2}.png`,
}));
export default class AppHome extends Component {
    render() {
        return (
            <div>
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#fff'}}
                    >住吧家居
                </NavBar>                
                <Carousel
                    autoplay={true}
                    infinite
                    // beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                    // afterChange={index => console.log('slide to', index)}
                    >
                    {[1,2].map(val => (
                        <a
                        key={val}
                        href="http://www.alipay.com"
                        style={{ display: 'inline-block', width: '100%', height: 200 }}
                        >
                        <img
                            src={`./images/${val}.png`}
                            alt=""
                            style={{ width: '100%', verticalAlign: 'top' }}
                            onLoad={() => {
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                            }}
                        />
                        </a>
                    ))}
                </Carousel>
                <div style={{padding:'20px 20px',background:'white'}}>
                    <Grid style={{background:'white'}} data={data1}
                        columnNum={3}                        
                        renderItem={dataItem => (
                            <div style={{ padding: '0 5px' }}>                                
                                <img src={dataItem.src} style={{ width: '100%', height: 'auto' }} alt="" />
                            </div>
                        )}
                        />
                </div>
                <div>
                    <p></p>
                    <h1><span style={{color:'#3fcccb'}}>|</span> 热门推荐</h1>
                    <p></p>
                </div>
                <div>
                    <img style={{width:'100%',height:'auto'}} src='./images/6.png'/>
                </div>
        </div>

        )
    }
}